import Image from 'next/image'
import Link from 'next/link'

export default function Header() {
  return (
    <header className="bg-background py-4 px-6 flex items-center justify-between border-b border-border">
      {/* Left side: Logo */}
      <div className="flex items-center">
        <Image src="/media/logo.png" alt="Logo" width={50} height={50} />
        <span className="ml-2 text-xl font-bold text-primary">Sigma Solution</span>
      </div>

      {/* Middle: Nav Bar */}
      <nav className="hidden md:flex space-x-8">
        <Link href="/" className="text-primary border-b-2 border-primary pb-1 font-semibold">Home</Link>
        <Link href="/products" className="text-muted-foreground hover:text-primary pb-1">Our Products</Link>
        <Link href="/team" className="text-muted-foreground hover:text-primary pb-1">Core Team</Link>
        <Link href="/faqs" className="text-muted-foreground hover:text-primary pb-1">FAQs</Link>
        <Link href="/contact" className="text-muted-foreground hover:text-primary pb-1">Contact Us</Link>
      </nav>

      {/* Right side: Sign In Button */}
      <div>
        <button className="bg-primary text-primary-foreground px-4 py-2 rounded hover:bg-primary/90">
          Sign In
        </button>
      </div>
    </header>
  )
}