import Image from 'next/image'
import Link from 'next/link'

export default function Intro() {
  return (
    <section className="py-16 px-6 bg-background">
      <div className="max-w-7xl mx-auto flex flex-col md:flex-row items-center">
        {/* Left side: Text */}
        <div className="md:w-1/2 mb-8 md:mb-0">
          <h1 className="text-4xl md:text-5xl font-bold text-foreground mb-4">
            Power Platform Services Partner
          </h1>
          <p className="text-lg text-muted-foreground mb-6">
            PowerPlatform Service Provider That You Can Absolutely Trust for Quality Product.
          </p>
          <Link href="/products" className="bg-secondary text-secondary-foreground px-6 py-3 rounded hover:bg-secondary/80 inline-block">
            Explore Now
          </Link>
        </div>

        {/* Right side: Image */}
        <div className="md:w-1/2 flex justify-center">
          <Image
            src="/media/PowerPlatform2.png"
            alt="PowerPlatform"
            width={500}
            height={300}
            className="rounded-lg"
          />
        </div>
      </div>
    </section>
  )
}